import React, { Component } from "react";
import ReactDOM from "react-dom";

class CommentForm extends Component {
  constructor(props) {
    super(props);
  }

  handleSubmit(e) {
    e.preventDefault();
    let author = ReactDOM.findDOMNode(this.refs.author).value.trim();
    let text = ReactDOM.findDOMNode(this.refs.text).value.trim();
    if (!text || !author) {
      alert("Please enter your name and comment");
      return;
    }
    this.props.onCommentSubmit({ author: author, text: text });
    ReactDOM.findDOMNode(this.refs.author).value = "";
    ReactDOM.findDOMNode(this.refs.text).value = "";
    ReactDOM.findDOMNode(this.refs.author).focus();
  }

  render() {
    return (
      <div className="commentForm panel panel-default">
        <div className="panel-body">
          <form className="form" onSubmit={this.handleSubmit.bind(this)}>
            <input
              className="form-control"
              type="text"
              placeholder="Your name"
              ref="author"
            />
            <br />
            <input
              className="form-control"
              type="text"
              placeholder="Say somthing here..."
              ref="text"
            />
            <br />
            <input className="btn btn-default" type="submit" value="Post" />
          </form>
        </div>
      </div>
    );
  }
}
export default CommentForm;
