import React, { Component } from "react";
import $ from "jquery";
import CommentList from "./CommentList.jsx";
import CommentForm from "./CommentForm.jsx";
class CommentBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = { comments: [] };
  }

  componentDidMount() {
    $.ajax({
      url: this.props.url,
      dataType: "json",
      cache: false,
      success: function (resp) {
        this.setState({ comments: resp });
      }.bind(this),
      error: function (xhr, status, err) {
        console.error(this.props.url, status, err.toString());
      }.bind(this),
    });
  }

  handleCommentSubmit(newComment) {
    let comments = this.state.comments;
    let newComments = comments.concat([newComment]);
    this.setState({ comments: newComments });
  }

  render() {
    return (
      <div className="container">
        <div className="commentBox panel panel-default">
          <div className="panel-body">
            <h1>Feedback System for Project ID esfojusdhfjosdfjs</h1>
            <CommentList comments={this.state.comments} />
            <CommentForm
              onCommentSubmit={this.handleCommentSubmit.bind(this)}
            />
          </div>
        </div>
      </div>
    );
  }
}
export default CommentBox;
